import apiAuthorization from '../api/apiAuthorization';
import { router } from '../utilities/createRouter';
import { AuthorizationSignInInterface, AuthorizationSignUpInterface } from '../interfaces/interfaces';
//import { showPreloader, hidePreloader } from '../utils/preloader';
//import { handleError } from '../utils/apiHandler';
//import { IAuthApiSignIn, IAuthApiSignUp } from '../interfaces/IAuthApi';
//import { store } from '../store';



class AuthorizationController {
	public signIn(user: AuthorizationSignInInterface, callback) {
		//showPreloader();
		return apiAuthorization.signIn(user)
		.then((answer) => {
			if (answer.reason && callback){
				callback(answer.reason);
			} else {
				router.go('/messenger');
			}		
		})
		.catch((error) => {
			if (error.response && callback){
				callback(error.response);
			} else {
				router.go('/500');
			}			
		});
	}

	public signUp(user: AuthorizationSignUpInterface) {
		//showPreloader();
		console.log(user)
		return apiAuthorization.signUp(user)
		.then((answer) => {
			console.log(answer)
			router.go('/messenger');
		})
		.catch(() => {
			router.go('/500');
			//hidePreloader();
		});
	}

	public signOut() {
		return apiAuthorization.signOut()
		.then(() => {
			//localStorage.removeItem('last-select-chat-id');
			router.go('/authorization');
		});
	}

	public checkAuth() {
		return apiAuthorization.checkAuth()
		.then((user) => {
			console.log(user)
			//store.setState({
			//  currentUser: user,
			//});
		})
		.catch((error) => {
			console.log(error)
			//handleError(error);
			router.go('/sign-in');
		});
	}
}

export default new AuthorizationController();